.cmp-wizard-form {
  .cmp-text {
    font-size: 1rem;
    color: #000c21;
    letter-spacing: 0;
    margin: 0;
    /* line-height: 22px; */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  .cmp-text-component-container .cmp-text > p {
    margin: 0;
  }

  .cmp-text.cmp-text--gray {
    color: #555555;
  }

  .cmp-text.cmp-text--bold {
    font-size: 0.875rem;
    font-weight: 600;
    line-height: 19px;
  }

  .cmp-text ul {
    list-style: none;
    padding-left: 2.5%;
  }

  .cmp-text ul li:before {
    content: 'â€¢';
    font-size: 1.25rem;
    padding-right: 20px;
    line-height: 22px;
  }
}
