$main-datepicker-color: #000c21;
$datepicker-label-color: rgba(0, 0, 0, 0.6);
$datepicker-error-color: #d32f2f;


.error:nth-child(1) {
  &.focused .SingleDatePickerInput__withBorder {
    border-bottom: 2px solid $datepicker-error-color !important;
  }
}

.datepicker-container {
  position: relative;

  .label-simulated-placeholder {
    position: absolute;
    bottom: 24.3px;
    transition: transform 200ms cubic-bezier(0.0, 0, 0.2, 1) 0ms, max-width 200ms cubic-bezier(0.0, 0, 0.2, 1) 0ms;
    transform: translate(0, 20px) scale(1);
    left: 0px;
    z-index: 99;
    font-size: 16px;
    color: $datepicker-label-color;
    font-family: Open Sans;
    font-weight: 400;

  }

  .label-placeholder {
    display: none;
  }

  label {
    color: $datepicker-label-color;
  }

  >div {
    margin: 1rem 0;
    margin-bottom: 0;
    width: 100%;
  }

  .DateInput_input {
    outline: none;

    &::placeholder {
      display: none;
      color: transparent;
    }
  }

  input#single-date-picker::placeholder {
    color: transparent;
    opacity: 0;
    visibility: hidden;
    transition: all .2s ease;
  }

}

.helper-text {
  color: $datepicker-label-color;
  font-family: Open Sans;
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 1.66;
  text-align: left;
  margin-top: 3px;
  margin-right: 0;
  margin-bottom: 0;
  margin-left: 0;
}

.focused {
  .SingleDatePickerInput__withBorder {
    border-color: darken($color: #949494, $amount: 100) !important;
    border-width: 2px;
  }

  .label-simulated-placeholder {
    transition: transform 200ms cubic-bezier(0.0, 0, 0.2, 1) 0ms, max-width 200ms cubic-bezier(0.0, 0, 0.2, 1) 0ms;
    transform: translate(-29px, -5.5px) scale(0.75);
    left: 3px;
  }

  .label-placeholder {
    // display: none;
    opacity: 0.6;
    position: absolute;
    z-index: 999;
    bottom: 12px;
    display: block;
  }

  .DateInput_input {
    outline: inherit;

    &::placeholder {
      display: block;
      color: inherit;
      opacity: 0.7;
    }
  }

  input#single-date-picker::placeholder {
    color: $datepicker-label-color;
    opacity: 0.62;
    visibility: visible;
    font-size: 16px;
    position: absolute;
    bottom: 0.8px;
  }
}

label.error-required {
  color: $datepicker-error-color;
  font-weight: 400;
  font-size: 0.75rem;
  font-family: inherit;
  line-height: 1.66;
  text-align: left;
  display: block;
  margin-top: 3px;
}

