.cmp-upload {
  position: relative;
}

.cmp-upload > .MuiBox-root {
  border-style: dashed;
}

.cmp-upload > .MuiBox-root ~ .MuiBox-root {
  border: none;
}

.cmp-upload.cmp-error .MuiBox-root {
  border-color: rgb(255, 0, 0);
}

.cmp-upload.cmp-error .MuiBox-root .MuiSvgIcon-root {
  fill: rgb(255, 0, 0);
}

.cmp-upload.cmp-error .MuiBox-root .MuiButton-root {
  background-color: rgb(255, 0, 0);
}

.cmp-upload.cmp-error .MuiBox-root:hover {
  border-color: rgba(0, 0, 0, 0.87);
}

.cmp-upload svg[data-testid='CloudUploadIcon'] {
  color: rgb(0 85 140);
}
.cmp-upload span.MuiTypography-caption {
  color: rgb(176, 176, 176);
}
.cmp-upload li {
  width: 100%;
}
.cmp-upload [role='button'] {
  max-width: 500px;
  margin: 0 auto;
  display: flex;
  border: none;
  justify-content: left;
}
.cmp-upload svg[data-testid='UploadFileIcon'] {
  color: rgb(0 85 140);
}
.cmp-upload .MuiChip-label {
  align-self: start;
  font-weight: bold;
}
.cmp-upload svg[data-testid='CancelIcon'] {
  margin-top: auto;
  margin-bottom: auto;
}
.cmp-upload button {
  background-color: rgb(0, 85, 145);
}
#attach {
  position: absolute;
  color: rgb(0, 85, 145);
  top: 165px;
  left: 520px;
}

.cmp-upload__upload-complete {
  width: 50%;
  margin: 0 25% 0 25%;
}
.cmp-upload__upload-complete-container {
  position: absolute;
  width: 50%;
  bottom: 10px;
}

.remove {
  color: rgb(176, 176, 176);
  text-decoration: underline;
}
.cmp-upload span.MuiChip-label {
  color: #000c21;
  font-weight: bold;
  margin-top: auto;
  margin-bottom: auto;
}
.MuiButtonBase-root .MuiChip-outlined {
  width: 100%;
}
.cmp-wizard-form-container #attach {
  top: 162px;
  left: 135px;
}
.cmp-wizard-form-container #upload {
  top: 225px;
  left: 110px;
}
