@import '../../theme/variables';
@import '../../theme/breakpoints';
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap');

//TODO: move this to another file
//In touch overrides
html {
  font-size: unset;
}

.cmp-bottom-button {
  font-size: 15px !important;
  &.cmp-bottom-button__invalid-inputs {
    color: $white-color !important;
    background-color: $disabled-bg-color !important;
  }
}

#content .content-wrap > div > div:not(:empty) {
  padding-bottom: unset;
}

.cmp-wizard-form-container input,
.cmp-wizard-form-container select,
.cmp-wizard-form-container textarea {
  max-width: 100%;
}

//Regular form styles
@mixin Center {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin column($gap: '2rem') {
  display: flex;
  flex-direction: column;
  gap: $gap;
}

.cmp-wizard-form-container {
  padding-bottom: 16px;
  background-color: $form-background;
  width: 100%;
  height: max-content;

  button {
    text-transform: unset;
  }

  .cmp-wizard-form {
    font-family: 'Open Sans', sans-serif;
    margin: 0 auto;
    max-width: 1024px;

    .MuiFormControl-root {
      width: 100%;
    }

    &__header {
      display: flex;
      margin: 1rem 0;

      h2.cmp-title {
        width: 15rem;

        @include mobile {
          width: 100%;
        }
      }

      .wizard-form-stepper {
        flex: 1;

        span.MuiStepLabel-iconContainer.MuiStepLabel-alternativeLabel {
          svg circle {
            fill: $seconday-color;
          }

          svg text {
            fill: $primary-color;
            stroke-width: 3px;
          }
        }

        svg.MuiSvgIcon-root.MuiSvgIcon-fontSizeMedium.MuiStepIcon-root {
          width: 3em;
          height: 1.2em;
          overflow: visible;
        }

        .Mui-disabled {
          circle {
            fill: $white-color !important;
            stroke: rgb(202, 202, 202);
          }

          text {
            fill: rgb(153, 153, 153) !important;
          }
        }

        .Mui-completed {
          color: $seconday-color;

          span.MuiStepLabel-label.MuiStepLabel-alternativeLabel {
            color: $primary-color;
          }
        }

        .Mui-active {
          span.MuiStepLabel-label.Mui-active.MuiStepLabel-alternativeLabel {
            font-weight: bolder;
            font-size: 18px;
          }

          circle {
            fill: $white-color !important;
            stroke: $seconday-color;
            stroke-width: 2px;
            r: 12;
          }

          .css-gawbvz-MuiStepIcon-text {
            fill: $primary-color;
          }
        }
      }

      .each-step {
        cursor: pointer;
      }
    }

    &__body {
      background: $white-color;

      & > .cmp-wizard-form__panel.active {
        display: block;

        & > div:first-child {
          @include column(2rem);
        }
      }
    }

    &__back-nav {
      display: flex;
      align-items: center;
      cursor: pointer;
      padding-top: 1rem;
      color: #00558c;
      font-weight: bold;

      span {
        font-size: 1rem;
      }
    }

    &__panel {
      display: none;
    }

    &__result-panel {
      display: flex;
      height: 604px;
      justify-content: center;
      align-items: center;
    }

    &__result-panel * {
      padding-bottom: 6px;
    }

    &__result-text {
      max-width: 65%;
    }

    &__result-icon {
      margin-right: 24px;
      img {
        height: 80px;
      }
    }

    &__try-again-button {
      border-radius: unset;
      padding-top: 8px;
    }
  }
}
